import { Typewriter } from 'react-simple-typewriter'

export const Introduction = () => {
    return (
        <section id="introduction" className="section-slide-2">
            <div className="section-slide-2-content">
                <h1 className="title">!   <span className=""></span></h1>
                <h1 className="fg-secondary"> 
                    <span className="fg-accent">
                        <Typewriter
                        words={['  3D ', '   ', '   VFX', '   ']}
                        loop={false}
                        cursor
                        cursorStyle='_'
                        typeSpeed={30}
                        deleteSpeed={20}
                        delaySpeed={3000}
                        />
                    </span>
                </h1>
                <br/>
                <h5 className="fg-primary d-inline-block me-2 mt-1 text-center align-middle">  ?</h5>
                <button type="submit" className="btn btn-outline-success rounded-pill" onClick={() => document.getElementById('about')?.scrollIntoView()}> !</button>
                <br/>
                <br/>
                <br/>
                <a href="#contact"><h5 className="fg-accent d-inline-block me-2 mt-1 text-center align-middle">         </h5></a>
            </div>
        </section>
    )
}

export default Introduction;